// Mobile first

@mixin vp-375 {
  @media (min-width: $vp-375) {
    @content;
  }
}

@mixin vp-768 {
  @media (min-width: $vp-768) {
    @content;
  }
}

@mixin vp-1024 {
  @media (min-width: $vp-1024) {
    @content;
  }
}

@mixin vp-1200 {
  @media (min-width: $vp-1200) {
    @content;
  }
}

@mixin vp-1440 {
  @media (min-width: $vp-1440) {
    @content;
  }
}

@mixin vp-1920 {
  @media (min-width: $vp-1920) {
    @content;
  }
}

// ---------------------------------

@mixin font-face($family, $src, $weight: normal, $style: normal, $display: swap) {
    @font-face {
        font-family: $family;
        src: url('#{$src}.woff2') format('woff2'),
             url('#{$src}.woff') format('woff');
        font-style: $style;
        font-weight: $weight;
        font-display: $display;
    }
}

@mixin expand-interactive-area($width, $height) {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: $width;
      height: $height;
      transform: translate(-50%, -50%);
    }
}
